<!--
 * @Description: 首页轮播图组件
 * @Date: 2020-01-04 15:58:47
 * @LastEditors  : Astronautics across the sea of stars
 * @LastEditTime : 2020-01-06 17:03:21
 -->
<template>
  <v-card elevation="4" max-width="444" class="mx-auto">
    <!-- <v-system-bar lights-out> nihao !!! </v-system-bar> -->
    <v-carousel
      :continuous="true"
      :cycle="cycle"
      :show-arrows="false"
      hide-delimiter-background
      delimiter-icon="remove"
      :interval="5000"
      height="130"
    >
      <v-carousel-item v-for="(slide, i) in slides" :key="i">
        <v-sheet :color="colors[i]" height="100%" tile>
          <v-row class="fill-height" align="center" justify="center">
            <div class="display-3">{{ slide }} Slide</div>
          </v-row>
        </v-sheet>
      </v-carousel-item>
    </v-carousel>
  </v-card>
</template>

<script>
export default {
  data() {
    return {
      colors: [
        "green",
        "secondary",
        "yellow darken-4",
        "red lighten-2",
        "orange darken-1"
      ],
      cycle: true,
      slides: ["First", "Second", "Third", "Fourth", "Fifth"]
    };
  }
};
</script>